import { motion } from 'framer-motion';
import React, { useEffect, useRef, useState } from 'react';
import { umkmData } from '../data/umkm';

const UltimateAnimatedBackground = () => {
	const canvasRef = useRef(null);
	const [mousePos, setMousePos] = useState({ x: 0, y: 0 });
	const particlesRef = useRef([]);
	const imagesRef = useRef([]);
	const animationFrameRef = useRef();

	// Get unique UMKM images - gunakan unsplash karena reliable
	const umkmImages = umkmData
		.filter((umkm) => umkm.images && umkm.images.length > 0)
		.slice(0, 12)
		.map((umkm) => umkm.images[0]); // Ambil 12 UMKM pertama

	// Track mouse position
	useEffect(() => {
		const handleMouseMove = (e) => {
			setMousePos({ x: e.clientX, y: e.clientY });
		};
		window.addEventListener('mousemove', handleMouseMove);
		return () => window.removeEventListener('mousemove', handleMouseMove);
	}, []);

	// Initialize Canvas Particles
	useEffect(() => {
		const canvas = canvasRef.current;
		if (!canvas) return;

		const ctx = canvas.getContext('2d');
		const resizeCanvas = () => {
			canvas.width = window.innerWidth;
			canvas.height = window.innerHeight;
			initParticles();
		};

		// Particle class
		class Particle {
			constructor(image, index) {
				this.image = image;
				this.size = 80 + Math.random() * 40; // 80-120px (EXTRA BESAR!)
				this.x = Math.random() * canvas.width;
				this.y = Math.random() * canvas.height;
				this.speedX = (Math.random() - 0.5) * 0.8;
				this.speedY = (Math.random() - 0.5) * 0.8;
				this.opacity = 0.85 + Math.random() * 0.15; // 0.85-1.0 (SUPER VISIBLE!)
				this.rotation = Math.random() * Math.PI * 2;
				this.rotationSpeed = (Math.random() - 0.5) * 0.03;
				this.scale = 1;
				this.targetScale = 1;
				this.pulsePhase = Math.random() * Math.PI * 2; // For pulse animation
			}

			update(mouseX, mouseY) {
				// Mouse interaction - repel particles (STRONGER!)
				const dx = this.x - mouseX;
				const dy = this.y - mouseY;
				const distance = Math.sqrt(dx * dx + dy * dy);
				const minDistance = 250; // Even bigger range!

				if (distance < minDistance) {
					const force = (minDistance - distance) / minDistance;
					const angle = Math.atan2(dy, dx);
					this.x += Math.cos(angle) * force * 10; // 10x force!
					this.y += Math.sin(angle) * force * 10;
					this.targetScale = 1.2; // Scale up on hover!
				} else {
					this.targetScale = 1;
				}

				// Smooth scale transition
				this.scale += (this.targetScale - this.scale) * 0.1;

				// Pulse animation
				this.pulsePhase += 0.03;

				// Move particles
				this.x += this.speedX;
				this.y += this.speedY;
				this.rotation += this.rotationSpeed;

				// Boundary check
				if (this.x < -this.size) this.x = canvas.width + this.size;
				if (this.x > canvas.width + this.size) this.x = -this.size;
				if (this.y < -this.size) this.y = canvas.height + this.size;
				if (this.y > canvas.height + this.size) this.y = -this.size;
			}

			draw(ctx) {
				if (!this.image.complete || !this.image.naturalWidth) return;

				const pulse = Math.sin(this.pulsePhase) * 0.05 + 1; // Subtle pulse
				const currentSize = this.size * this.scale * pulse;

				// OUTER GLOW - Multi-color layers untuk depth & visibility
				// Layer 4: Blue outer glow (brand color)
				ctx.save();
				ctx.globalAlpha = this.opacity * 0.5;
				ctx.beginPath();
				ctx.arc(this.x, this.y, currentSize / 2 + 42, 0, Math.PI * 2);
				ctx.strokeStyle = '#2563eb'; // Bright blue
				ctx.lineWidth = 10;
				ctx.shadowColor = '#2563eb';
				ctx.shadowBlur = 35;
				ctx.stroke();
				ctx.restore();

				// Layer 3: Golden/Orange glow (brand accent)
				ctx.save();
				ctx.globalAlpha = this.opacity * 0.6;
				ctx.beginPath();
				ctx.arc(this.x, this.y, currentSize / 2 + 28, 0, Math.PI * 2);
				ctx.strokeStyle = '#f59e0b'; // Orange
				ctx.lineWidth = 12;
				ctx.shadowColor = '#f59e0b';
				ctx.shadowBlur = 30;
				ctx.stroke();
				ctx.restore();

				// Layer 2: Yellow glow (signature)
				ctx.save();
				ctx.globalAlpha = this.opacity * 0.7;
				ctx.beginPath();
				ctx.arc(this.x, this.y, currentSize / 2 + 16, 0, Math.PI * 2);
				ctx.strokeStyle = '#fbbf24'; // Bright yellow
				ctx.lineWidth = 10;
				ctx.shadowColor = '#fbbf24';
				ctx.shadowBlur = 25;
				ctx.stroke();
				ctx.restore();

				// Layer 1: White/Cyan inner glow (pop!)
				ctx.save();
				ctx.globalAlpha = this.opacity * 0.8;
				ctx.beginPath();
				ctx.arc(this.x, this.y, currentSize / 2 + 8, 0, Math.PI * 2);
				ctx.strokeStyle = '#06b6d4'; // Cyan/turquoise
				ctx.lineWidth = 8;
				ctx.shadowColor = '#06b6d4';
				ctx.shadowBlur = 20;
				ctx.stroke();
				ctx.restore();

				// MAIN IMAGE with scale
				ctx.save();
				ctx.globalAlpha = this.opacity;
				ctx.translate(this.x, this.y);
				ctx.rotate(this.rotation);
				ctx.scale(this.scale * pulse, this.scale * pulse);

				// Draw circular clipped image
				ctx.beginPath();
				ctx.arc(0, 0, this.size / 2, 0, Math.PI * 2);
				ctx.closePath();
				ctx.clip();

				try {
					ctx.drawImage(
						this.image,
						-this.size / 2,
						-this.size / 2,
						this.size,
						this.size
					);
				} catch (e) {
					console.error('Error drawing image:', e);
				}

				ctx.restore();

				// MAIN BORDER - Gradient-style double ring
				// Outer ring: Brand yellow
				ctx.save();
				ctx.globalAlpha = this.opacity;
				ctx.beginPath();
				ctx.arc(this.x, this.y, currentSize / 2 + 3, 0, Math.PI * 2);
				ctx.strokeStyle = '#f5cb3f'; // Brand yellow
				ctx.lineWidth = 5;
				ctx.shadowColor = '#f5cb3f';
				ctx.shadowBlur = 18;
				ctx.stroke();
				ctx.restore();

				// Inner ring: White with strong contrast
				ctx.save();
				ctx.globalAlpha = this.opacity;
				ctx.beginPath();
				ctx.arc(this.x, this.y, currentSize / 2, 0, Math.PI * 2);
				ctx.strokeStyle = '#ffffff';
				ctx.lineWidth = 7; // Extra thick white!
				ctx.shadowColor = '#1c3f80'; // Blue shadow untuk kontras
				ctx.shadowBlur = 15;
				ctx.shadowOffsetX = 2;
				ctx.shadowOffsetY = 2;
				ctx.stroke();
				ctx.restore();

				// ACCENT HIGHLIGHT - Top-left shine
				ctx.save();
				ctx.globalAlpha = this.opacity * 0.6;
				const highlightAngle = this.rotation + Math.PI * 0.25;
				const highlightX =
					this.x + Math.cos(highlightAngle) * (currentSize / 2.5);
				const highlightY =
					this.y + Math.sin(highlightAngle) * (currentSize / 2.5);
				ctx.beginPath();
				ctx.arc(highlightX, highlightY, currentSize / 8, 0, Math.PI * 2);
				ctx.fillStyle = '#ffffff';
				ctx.shadowColor = '#ffffff';
				ctx.shadowBlur = 10;
				ctx.fill();
				ctx.restore();
			}
		}

		// Load images with better handling
		const loadImages = () => {
			imagesRef.current = umkmImages.map((src) => {
				const img = new Image();
				// Handle CORS for external images
				if (
					src.includes('unsplash') ||
					src.includes('googleusercontent') ||
					src.includes('lh3')
				) {
					// For external images, don't use crossOrigin to avoid CORS issues
					img.src = src;
				} else {
					img.crossOrigin = 'anonymous';
					img.src = src;
				}

				// Debug log
				img.onerror = () => console.log('Failed to load image:', src);
				img.onload = () => console.log('Image loaded:', src);

				return img;
			});
		};

		// Initialize particles
		const initParticles = () => {
			particlesRef.current = [];
			const particleCount = Math.min(umkmImages.length, 12);

			imagesRef.current.forEach((img, index) => {
				if (index < particleCount) {
					particlesRef.current.push(new Particle(img, index));
				}
			});
		};

		// Animation loop
		const animate = () => {
			ctx.clearRect(0, 0, canvas.width, canvas.height);

			particlesRef.current.forEach((particle) => {
				particle.update(mousePos.x, mousePos.y);
				particle.draw(ctx);
			});

			animationFrameRef.current = requestAnimationFrame(animate);
		};

		loadImages();
		resizeCanvas();

		// Wait for images to load before starting animation
		Promise.all(
			imagesRef.current.map(
				(img) =>
					new Promise((resolve) => {
						if (img.complete) resolve();
						else img.onload = resolve;
					})
			)
		).then(() => {
			animate();
		});

		window.addEventListener('resize', resizeCanvas);

		return () => {
			window.removeEventListener('resize', resizeCanvas);
			if (animationFrameRef.current) {
				cancelAnimationFrame(animationFrameRef.current);
			}
		};
	}, [umkmImages, mousePos]);

	return (
		<div className="fixed inset-0 -z-10 overflow-hidden pointer-events-none">
			{/* Animated Gradient Background */}
			<div className="absolute inset-0 bg-gradient-to-br from-custom-bg via-blue-50 to-custom-bg dark:from-gray-900 dark:via-blue-950 dark:to-gray-900">
				<div className="absolute inset-0 bg-gradient-mesh-animated opacity-40"></div>
			</div>

			{/* Canvas for UMKM Image Particles - HIGHER Z-INDEX */}
			<canvas
				ref={canvasRef}
				className="absolute inset-0 w-full h-full"
				style={{
					mixBlendMode: 'normal',
					zIndex: 5,
					position: 'absolute',
				}}
			/>

			{/* Liquid Blob Shapes - Morphing */}
			<motion.div
				className="absolute top-20 -left-32 w-96 h-96 rounded-full blur-3xl"
				style={{
					background:
						'radial-gradient(circle, rgba(28,63,128,0.15) 0%, transparent 70%)',
				}}
				animate={{
					scale: [1, 1.3, 1.1, 1],
					x: [0, 50, -30, 0],
					y: [0, 30, -20, 0],
					borderRadius: [
						'60% 40% 30% 70%',
						'30% 60% 70% 40%',
						'50% 50% 50% 50%',
						'60% 40% 30% 70%',
					],
				}}
				transition={{
					duration: 20,
					repeat: Infinity,
					ease: 'easeInOut',
				}}
			/>

			<motion.div
				className="absolute bottom-32 -right-32 w-[30rem] h-[30rem] rounded-full blur-3xl"
				style={{
					background:
						'radial-gradient(circle, rgba(245,203,63,0.12) 0%, transparent 70%)',
				}}
				animate={{
					scale: [1, 1.2, 1.4, 1],
					x: [0, -60, 40, 0],
					y: [0, -40, 30, 0],
					borderRadius: [
						'40% 60% 70% 30%',
						'70% 30% 50% 50%',
						'50% 50% 30% 70%',
						'40% 60% 70% 30%',
					],
				}}
				transition={{
					duration: 25,
					repeat: Infinity,
					ease: 'easeInOut',
				}}
			/>

			<motion.div
				className="absolute top-1/2 left-1/2 w-[25rem] h-[25rem] rounded-full blur-3xl"
				style={{
					background:
						'radial-gradient(circle, rgba(59,130,246,0.1) 0%, transparent 70%)',
					transform: 'translate(-50%, -50%)',
				}}
				animate={{
					scale: [1, 1.5, 1.2, 1],
					rotate: [0, 180, 360],
					borderRadius: [
						'50% 50% 50% 50%',
						'30% 70% 70% 30%',
						'70% 30% 30% 70%',
						'50% 50% 50% 50%',
					],
				}}
				transition={{
					duration: 30,
					repeat: Infinity,
					ease: 'easeInOut',
				}}
			/>

			{/* Wave Shapes - Animated SVG */}
			<motion.div
				initial={{ opacity: 0 }}
				animate={{ opacity: 0.08 }}
				transition={{ duration: 1 }}
				className="absolute top-0 left-0 w-full">
				<svg
					viewBox="0 0 1440 320"
					className="w-full h-auto"
					preserveAspectRatio="none">
					<motion.path
						fill="#1c3f80"
						fillOpacity="0.3"
						d="M0,96L48,112C96,128,192,160,288,160C384,160,480,128,576,122.7C672,117,768,139,864,149.3C960,160,1056,160,1152,138.7C1248,117,1344,75,1392,53.3L1440,32L1440,0L1392,0C1344,0,1248,0,1152,0C1056,0,960,0,864,0C768,0,672,0,576,0C480,0,384,0,288,0C192,0,96,0,48,0L0,0Z"
						animate={{
							d: [
								'M0,96L48,112C96,128,192,160,288,160C384,160,480,128,576,122.7C672,117,768,139,864,149.3C960,160,1056,160,1152,138.7C1248,117,1344,75,1392,53.3L1440,32L1440,0L1392,0C1344,0,1248,0,1152,0C1056,0,960,0,864,0C768,0,672,0,576,0C480,0,384,0,288,0C192,0,96,0,48,0L0,0Z',
								'M0,64L48,80C96,96,192,128,288,128C384,128,480,96,576,90.7C672,85,768,107,864,117.3C960,128,1056,128,1152,106.7C1248,85,1344,43,1392,21.3L1440,0L1440,0L1392,0C1344,0,1248,0,1152,0C1056,0,960,0,864,0C768,0,672,0,576,0C480,0,384,0,288,0C192,0,96,0,48,0L0,0Z',
								'M0,96L48,112C96,128,192,160,288,160C384,160,480,128,576,122.7C672,117,768,139,864,149.3C960,160,1056,160,1152,138.7C1248,117,1344,75,1392,53.3L1440,32L1440,0L1392,0C1344,0,1248,0,1152,0C1056,0,960,0,864,0C768,0,672,0,576,0C480,0,384,0,288,0C192,0,96,0,48,0L0,0Z',
							],
						}}
						transition={{
							duration: 15,
							repeat: Infinity,
							ease: 'easeInOut',
						}}
					/>
				</svg>
			</motion.div>

			{/* Gradient Mesh Animation CSS */}
			<style jsx>{`
				.bg-gradient-mesh-animated {
					background: radial-gradient(
							circle at 20% 50%,
							rgba(28, 63, 128, 0.15) 0%,
							transparent 50%
						),
						radial-gradient(
							circle at 80% 80%,
							rgba(245, 203, 63, 0.12) 0%,
							transparent 50%
						),
						radial-gradient(
							circle at 40% 20%,
							rgba(59, 130, 246, 0.1) 0%,
							transparent 50%
						);
					animation: gradient-mesh-move 20s ease infinite;
				}

				@keyframes gradient-mesh-move {
					0%,
					100% {
						background-position: 0% 0%, 100% 100%, 50% 0%;
						background-size: 50% 50%, 60% 60%, 40% 40%;
					}
					33% {
						background-position: 100% 100%, 0% 0%, 100% 50%;
						background-size: 60% 60%, 50% 50%, 50% 50%;
					}
					66% {
						background-position: 50% 50%, 50% 0%, 0% 100%;
						background-size: 40% 40%, 70% 70%, 45% 45%;
					}
				}
			`}</style>
		</div>
	);
};

export default UltimateAnimatedBackground;
